<template>
  <div>

  </div>
</template>

<script>
  export default {
    name: "ObjectOriented",
    mounted() {
      // 
      // 
      function People1() {
        this.name = 'wangyu';
      }
      class People2 {
        constructor() {
          this.name = 'name';
        }
      }
      // 
      console.log(new People2());
      // 
      // 1. 
      function Parent1() {
        this.name = 'Parent1'
      }
      function Child1() {
        People1.call(this);
        this.type = 'child';
      }
      // 2. 
      function Parent2() {
        this.name = 'Parent2'
      }
      function Child2() {
        this.type = 'child2';
      }
      Child2.prototype = new Parent2();
      console.log(new Child2().__proto__);
      // 3. +
      function Parent3() {
        this.name = 'Parent3'
      }
      function Child3() {
        Parent3.call(this);
        this.type = 'child3';
      }
      // 
      // Child3.prototype = Parent3.prototype;
      // 
      Child3.prototype = Object.create(Parent3.prototype);
      Child3.prototype.constructor = Child3;
      let s1 = new Child3();
      let s2 = new Child3();
      console.log(s1);
    }
  }
</script>

<style scoped>

</style>



