/**
 * @description str
 * @method trim
 * @param {string} str
 * @param {number} type  1-  2-  3- 4-
 * @return {string} str
 */
const trim = (str, type) => {
  str = str.toString();
  switch (type) {
    case 1:
      return str.replace(/\s+/g, "");
    case 2:
      return str.replace(/(^\s*)|(\s*$)/g, "");
    case 3:
      return str.replace(/(^\s*)/g, "");
    case 4:
      return str.replace(/(\s*$)/g, "");
    default:
      return str;
  }
};
/**
 * @description 
 * @method replaceStr
 * @param {string} str 
 * @param {string} startStr  
 * @param {string} endStr  
 * @param {string} repStr 
 * @return {string} 
 * @example replaceStr("wangbilibiliyu","wang","yu","acfun") --> "wangacfunyu"
 * */
const replaceStr = (str,startStr,endStr,repStr) => {
  let str1 = '',str2 = '';
  str1 = str.match(new RegExp(startStr + '(\\S*)' + endStr))[1];
  str2 = str.replace(new RegExp(str1),repStr);
  return str2;
};
/**
 * @description 
 * @param type {number} 0 -- , 1 -- , 2 -- , 3 -- , 4 -- , 5 -- html, 6 -- 
 * @param str {string} 
 * @param replaceStr {string} 
 * @return {string}
 */
const	filterString = (type,str,replaceStr = '') => {
  switch (type){
    case 0:
      return str.replace(/[a-z]/g, replaceStr);
    case 1:
      return str.replace(/[A-Z]/g, replaceStr);
    case 2:
      return str.replace(/[a-zA-Z]/g, replaceStr);
    case 3:
      return str.replace(/[0-9]/g, replaceStr);
    case 4:
      return str.replace(/[\u4E00-\u9FA5]/g, replaceStr);
    case 5:
      return str.replace(/<\/?[^>]*>/g, replaceStr);
    case 6:
      return str.replace(/[^0-9A-Za-z\\s]/g, replaceStr);
    default:
      return 'error';
  }
};

