/**
 * @description 
 * @param data 
 * @param {String} type ,,
 * @return {String/Boolean} /
 */
const isType = (data,type) => {
  if (!type) return Object.prototype.toString.call(data).match(/\s(.*)]/)[1];
  let _type = type.toLowerCase().split(',');
  let typeObj = {
    'string': '[object String]',
    'number': '[object Number]',
    'boolean': '[object Boolean]',
    'null': '[object Null]',
    'function': '[object Function]',
    'array': '[object Array]',
    'object': '[object Object]',
    'symbol': '[object Symbol]'
  };
  let result = false;
  for (let item of _type){
    if (typeObj[item] && typeObj[item] === Object.prototype.toString.call(data)){
      result = true;
    }
  }
  return result;
};

/**
 * @description ,
 * @param type {string} email tel phone url number english numen chinese lower upper reg
 * @param str {string} 
 * @param rex {string} type=reg,rex
 * @return {Boolean}  true/false
 */
const checkType = (str,type,rex) => {
  let utils = {
    isNumber: function (val) {
      let regPos = /^\d+(\.\d+)?$/; //
      let regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //
      if(regPos.test(val) || regNeg.test(val)) {
        return true;
      } else {
        return false;
      }
    },
    // 
    isTel : function (val) {
      let reg = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    // 
    isPhone : function (val) {
      let reg = /^1(3|4|5|7|8)\d{9}$/;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isUrl : function (val) {
      let reg = /^[a-zA-z]+:\/\/(\w+(-\w+)*)(\.(\w+(-\w+)*))*(\?\S*)?$/;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isEmail : function (val) {
      let reg = /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isEnglish : function (val) {
      let reg = /[a-zA-Z]/g;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isNumen : function (val) {
      let reg = /[0-9A-Za-z\\s]/g;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isLower : function (val) {
      let reg = /[a-z]/g;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    isUpper : function (val) {
      let reg = /[A-Z]/g;
      if (reg.test(val)){
        return true;
      }else {
        return false;
      }
    },
    // 
    isChinese: function (val) {
      let reg = /[^\u4e00-\u9fa5]/g;
      if (reg.test(val)){
        return false;
      }else {
        return true;
      }
    }
  };
  switch (type){
    case 'reg':
      if (rex){
        if (new RegExp(rex).test(str)){
          return true;
        }else {
          return false;
        }
      }else {
        return false;
      }
    case 'email':
      return utils.isEmail(str);
    case 'tel':
      return utils.isTel(str);
    case 'phone':
      return utils.isPhone(str);
    case 'url':
      return utils.isUrl(str);
    case 'number':
      return utils.isNumber(str);
    case 'english':
      return utils.isEnglish(str);
    case 'numen':
      return utils.isNumen(str);
    case 'chinese':
      return utils.isChinese(str);
    case 'lower':
      return utils.isLower(str);
    case 'upper':
      return utils.isUpper(str);
    default:
      return false;
  }
};

