/**
 * @description url
 * @method getUrlParam
 * @param {string} paramName 
 * @return {string} 
 */
const getUrlParam = paramName => {
  let url = document.location.toString();
  let arrObj = url.split("?");
  if (arrObj.length > 1) {
    let arrPara = arrObj[1].split("&"),arr;
    for (let i = 0; i < arrPara.length; i++) {
      arr = arrPara[i].split("=");
      if (arr != null && arr[0] == paramName) {
        return arr[1];
      }
    }
    return "";
  }else {
    return "";
  }
};

/**
 * @description 
 * @method formatTimestamp
 * @param {Boolean} isNow ,false
 * @param {string} rules ,YYYY-MM-DD hh:mm:ss W
 * @param {number} timestamp ,isNowtrue
 * @return {string} 
 * @example getNowTime("YYYY-MM-DD hh:mm:ss W") --> "2019-02-15 15:43:24 "
 */
const formatTimestamp = ({ isNow,rules,timestamp }) => {
  let date,
      str = rules ? rules : "YYYY-MM-DD hh:mm:ss W",
      _isNow = typeof(isNow) == "undefined" ? false : isNow,
      _timestamp = timestamp ? timestamp : 0,
      week = ['', '', '', '', '', '', ''];
  if (_isNow){
    date = new Date();
  }else {
    if (_timestamp.toString().length === 10){
      date = new Date(_timestamp*1000);
    }
    if (_timestamp.toString().length === 13) {
      date = new Date(_timestamp);
    }
  }
  str = str.replace(/yyyy|YYYY/, date.getFullYear());
  str = str.replace(/yy|YY/, (date.getYear() % 100) > 9 ? (date.getYear() % 100).toString() : '0' + (date.getYear() % 100));

  str = str.replace(/MM/, date.getMonth() > 8 ? (date.getMonth() + 1).toString() : '0' + (date.getMonth() + 1));
  str = str.replace(/M/g, date.getMonth() + 1);

  str = str.replace(/w|W/g, week[date.getDay()]);

  str = str.replace(/dd|DD/, date.getDate() > 9 ? date.getDate().toString() : '0' + date.getDate());
  str = str.replace(/d|D/g, date.getDate());

  str = str.replace(/hh|HH/, date.getHours() > 9 ? date.getHours().toString() : '0' + date.getHours());
  str = str.replace(/h|H/g, date.getHours());
  str = str.replace(/mm/, date.getMinutes() > 9 ? date.getMinutes().toString() : '0' + date.getMinutes());
  str = str.replace(/m/g, date.getMinutes());

  str = str.replace(/ss|SS/, date.getSeconds() > 9 ? date.getSeconds().toString() : '0' + date.getSeconds());
  str = str.replace(/s|S/g, date.getSeconds());

  return str;
};

/**
 * @description 
 * @method arrayToObject
 * @param {Array} array 
 * @return {Object} 
 */
const arrayToObject = array => {
  let keyItem = [],keys,result = {};
  keys = Object.keys(array[0]);
  keys.forEach(function (item,index) {
    let obj = {};
    obj.key = item;
    obj.val = [];
    keyItem.push(obj);
  });

  array.forEach(function (item,index) {
    keys.forEach(function (item0,index0) {
      keyItem[index0].val.push(item[keyItem[index0].key])
    })
  });

  keyItem.forEach(function (item,index) {
    result[item.key] = item.val;
  });
  console.log(result);
  return result;
};

/**
 * @description 
 * @param {Boolean} options.isNow false
 * @param {String} options.type year/month/day/hour/minute/second
 * @param {number} options.offset
 * @param {Boolean} options.isAdd
 * @return {number} (13)
 */
const getDateTime = ({isNow,type,offset,isAdd}) => {
  let _isNow = typeof(isNow) == "undefined" ? false : isNow,
      _type = type,
      _offset = offset,
      _isAdd = isAdd,
      now = new Date().getTime(),
      oneSecond = 1000,
      oneMinute = 60 * oneSecond,
      oneHour = 60 * oneMinute,
      oneDay = 24 * oneHour,
      oneMonth = 30 * oneDay,
      oneYear = 12 * oneMonth;
  if (_isNow){
    return now;
  }else {
    switch (_type){
      case 'year':
        if (_isAdd){
          return new Date(now + (oneYear * _offset)).getTime();
        }else {
          return new Date(now - (oneYear * _offset)).getTime();
        }
      case 'month':
        if (_isAdd){
          return new Date(now + (oneMonth * _offset)).getTime();
        }else {
          return new Date(now - (oneMonth * _offset)).getTime();
        }
      case 'day':
        if (_isAdd){
          return new Date(now + (oneDay * _offset)).getTime();
        }else {
          return new Date(now - (oneDay * _offset)).getTime();
        }
      case 'hour':
        if (_isAdd){
          return new Date(now + (oneHour * _offset)).getTime();
        }else {
          return new Date(now - (oneHour * _offset)).getTime();
        }
      case 'minute':
        if (_isAdd){
          return new Date(now + (oneMinute * _offset)).getTime();
        }else {
          return new Date(now - (oneMinute * _offset)).getTime();
        }
      case 'second':
        if (_isAdd){
          return new Date(now + (oneSecond * _offset)).getTime();
        }else {
          return new Date(now - (oneSecond * _offset)).getTime();
        }
      default:
        return now;
    }
  }
};

/**
 * @description 
 * @param {number} timestamp 
 * @return {number} 
 */
const getMonthDays = timestamp => {
  let date = new Date(timestamp),year,mouth,days;
  if (timestamp.toString().length === 10){
    date = new Date(timestamp*1000);
  }else if (timestamp.toString().length === 13){
    date = new Date(timestamp);
  }else {
    return '';
  }
  year = date.getFullYear();
  mouth = date.getMonth() + 1;
  //
  if (mouth == 2) {
    days = (year%4==0 && year%100==0 && year%400==0) || (year%4==0 && year%100!=0) ? 28 : 29
  } else if (mouth == 1 || mouth == 3 || mouth == 5 || mouth == 7 || mouth == 8 || mouth == 10 || mouth == 12) {
    //1,3,5,7,8,10,12 .31
    days = 31
  } else {
    //30.
    days = 30
  }
  return days;
};

/**
 * @description 
 * @param n1
 * @param n2
 * @return {number}
 */
const randomNumber = (n1, n2) => {
  switch (arguments.length) {
    case 2:
      return Math.round(n1 + Math.random() * (n2 - n1));
    case 1:
      return Math.round(Math.random() * n1);
    default:
      return Math.round(Math.random() * 100000000);
  }
};
