<template>
  <div style="padding: 10px">
    <div>
      <div class="com-title">1. (w-radio)</div>
      <div style="width: 100px">
        <w-radio-group v-model="wRadioResult">
          <w-radio></w-radio>
          <w-radio></w-radio>
        </w-radio-group>
      </div>
    </div>
    <div>
      <div class="com-title">2. (w-switch)</div>
       <w-switch v-model="wSwitchResult1" :text="wSwitchResult2"></w-switch>
    </div>
    <div>
      <div class="com-title">3. (w-select)</div>
      <w-select v-model="wSelectResult1" :selectItems="wSelectResult2"></w-select>
    </div>
    <div>
      <div class="com-title">4. (w-toast)</div>
      <button class="button is-primary" @click="getToast"></button>
    </div>
    <div>
      <div class="com-title">5. (w-loading)</div>
      <button class="button is-primary" @click="showLoading"></button>
      <button class="button" @click="hideLoading"></button>
      <w-loading loadingText="" :loadingFlag="wLoadingResult"></w-loading>
    </div>
    <div>
      <div class="com-title">6. (w-alert)</div>
      <button class="button is-primary" @click="getAlert">1</button>
    </div>
    <div>
      <div class="com-title">7. (w-message-box)</div>
      <button class="button is-primary" @click="getMessageBox">2</button>
      <w-message-box
        boxTitle=""
        :boxVisible="boxVisible"
        :showCancel="showCancel"
        @cancel="cancelBox"
        @confirm="confirmBox">
        <div class="input-div">
          <label for="nike"></label>
          <input name="nike" type="text" id="nike">
        </div>
        <div class="input-div">
          <label for="age"></label>
          <input name="age" type="number" id="age">
        </div>
        <div class="input-div">
          <label for="sex"></label>
          <select name="sex" id="sex">
            <option value="0"></option>
            <option value="1"></option>
          </select>
        </div>
        <div class="input-div">
          <label for="card"></label>
          <input name="card" type="text" id="card">
        </div>
      </w-message-box>
    </div>
    <div>
      <div class="com-title">8. (w-pagination)</div>
      <div style="width: 730px">
        <w-pagination
          :cPage="1"
          :count="642"
          :pageSize="10"
          :pPages="5"
          @change="getPage"></w-pagination>
      </div>
    </div>
    <div>
      <div class="com-title">9. (w-date-picker)</div>
      <span class="com-title-span">-----:</span>
      <w-date-picker
        mode="datetime"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">--:</span>
      <w-date-picker
        mode="date"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">-:</span>
      <w-date-picker
        mode="month"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">:</span>
      <w-date-picker
        mode="year"
        @change="getDate">
      </w-date-picker>
    </div>
    <div>
      <div class="com-title">10. (w-upload-img)</div>
      <div>
        <w-upload-img @change="getList"></w-upload-img>
      </div>
    </div>
    <div>
      <div class="com-title">11. (w-print)</div>
      <button class="button is-primary" @click="print"></button>
    </div>
    <div>
      <div class="com-title">12. (w-scanner)</div>
      <button class="button is-primary" @click="goScanner"></button>
    </div>
  </div>
</template>

<script>
  import wDatePicker from './w-date-picker.vue';
  import wRadio from './w-radio/w-radio.vue';
  import wRadioGroup from './w-radio/w-radio-group.vue';
  import wSwitch from './w-switch.vue';
  import wSelect from './w-select';
  import wAlert from "../utils/alert";
  import wToast from "../utils/toast";
  import wLoading from "./w-loading";
  import wPagination from './w-pagination';
  import wUploadImg from './w-upload-img';
  import wPrint from "../utils/print";
  import wMessageBox from './w-message-box';
export default {
  name: 'HelloWorld',
  data () {
    return {
      testVal: '',
      wRadioResult: '',
      wSwitchResult1: 0,
      wSwitchResult2: ['',''],
      wSelectResult1: '',
      wSelectResult2: [
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: ''
        }
      ],
      wLoadingResult: false,
      printInfo: [
        {
          value1: '',
          value2: '',
          value3: '',
          value4: '',
          value5: '',
          value6: '',
          value7: '',
        }
      ],
      boxVisible: false,
      showCancel: true
    }
  },
  methods:{
    getDate(res){
      console.log(res)
    },
    getToast(){
      wToast({
        duration: 1000,
        position: 'middle',
        message: 'Toast'
      });
    },
    getAlert(){
      wAlert('toast');
    },
    showLoading(){
      this.wLoadingResult = true;
    },
    hideLoading(){
      this.wLoadingResult = false;
    },
    getMessageBox(){
      this.boxVisible = true;
    },
    getPage(res){
      console.log(res);
    },
    getList(res){
      console.log(res)
    },
    print(){
      wPrint({
        printInfo: this.printInfo
      })
    },
    goScanner(){
      this.$router.push({
        path: 'scan'
      })
    },
    cancelBox(){
      console.log('');
      this.boxVisible = false;
    },
    confirmBox(){
      console.log('');
      this.boxVisible = false;
    }
  },
  components: {
    wDatePicker,
    wRadio,
    wRadioGroup,
    wSwitch,
    wSelect,
    wLoading,
    wPagination,
    wUploadImg,
    wMessageBox
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .com-title{
    padding: 10px 5px;
    color: #333;
    font-size: 16px;
    font-weight: 800;
    margin-top: 20px;
  }
  .com-title-span{
    display: block;
    padding: 10px 5px;
    color: #333;
    font-size: 14px;
  }
  .button{
    font-size: 14px;
    -webkit-appearance: none;
    outline: 0;
    box-sizing: border-box;
    background-color: #fff;
    border-color: #dbdbdb;
    border-width: 1px;
    color: #363636;
    cursor: pointer;
    justify-content: center;
    padding: 6px 9px;
    text-align: center;
    white-space: nowrap;
    border-radius: 3px;
  }
  .button.is-primary{
    background-color: #409eff;
    border-color: transparent;
    color: #fff;
  }
  .input-div{
    width: 520px;
    margin-top: 30px;
  }
</style>



