<template>
  <div id="print-box">
    <iframe ref="printIframe" frameborder="0" scrolling="no" style="margin: 0px;padding: 0px;width: 0px;height: 0px;"></iframe>
    <div ref="odiv" style="display: none">
      <div v-for="(item,index) in printInfo" style="width: 377px;height: 378px;position: relative;box-sizing: border-box;">
        <div style="box-sizing: border-box;position:absolute;left:30px;top:60px;width: auto;font-size: 14px;text-align: left;"><span style="font-weight: 600">{{ item.value1 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:100px;width: auto;font-size: 14px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;text-align: left;"><span style="font-weight: 600">{{ item.value2 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:140px;width: auto;font-size: 14px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;text-align: left;"><span style="font-weight: 600;">{{ item.value3 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:180px;width: 48%;font-size: 14px;text-align: left;"><span style="font-weight: 600;">{{ item.value4 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:220px;width: 48%;font-size: 14px;text-align: left;"><span style="font-weight: 600">{{ item.value5 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:260px;width: 48%;font-size: 14px;text-align: left;"><span style="font-weight: 600">{{ item.value6 }}</span></div>
        <div style="box-sizing: border-box;position:absolute;left:30px;top:300px;width: 48%;font-size: 14px;text-align: left;"><span style="font-weight: 600">{{ item.value7 }}</span></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'w-print',
    data() {
      return {
        printInfo: []
      }
    },
    mounted(){
      this.$nextTick(() => {
        // dom
        setTimeout(() => {
          // domIframesrcdoc
          let printIframe = this.$refs.printIframe;
          let newstr = this.$refs.odiv.innerHTML;
          printIframe.setAttribute('srcdoc',newstr);
          printIframe.onload = function () {
            // iframedombodypadding margin
            printIframe.contentWindow.document.body.style.padding = '0px';
            printIframe.contentWindow.document.body.style.margin = '0px';
            // 
            printIframe.contentWindow.focus();
            printIframe.contentWindow.print();
          };
        },100);
      });
    }
  }
</script>


<style scoped>

</style>



